<template>
  <div class="upload_writ">
    <Form ref="formValidate"
      :model="formValidate" 
      :rules="ruleValidate" 
      :label-width="200">
      <FormItem label="文书类型：" prop="gender">
        <RadioGroup v-model="formValidate.gender">
          <Radio label="male">裁决书</Radio>
          <Radio label="female">调解书</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="文书来源：" prop="gender">
        <RadioGroup v-model="formValidate.gender">
          <Radio label="male">文书模板</Radio>
          <Radio label="female">文件上传</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="选择文书模版：" prop="city">
        <Select v-model="formValidate.city"
          style="width: 200px;"
          placeholder="请选择模板">
            <Option value="beijing">模版1</Option>
            <Option value="shanghai">模版2</Option>
            <Option value="shenzhen">模版3</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')">确定</Button>
        <Button style="margin-left: 8px">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  name: 'upload_writ',
  data () {
    return {
      formValidate: {
        name: '',
        mail: '',
        city: '',
        gender: '',
        interest: [],
        date: '',
        time: '',
        desc: ''
      },
      ruleValidate: {
        gender: [
            { required: true, message: 'Please select gender', trigger: 'change' }
        ],
        city: [
            { required: true, message: 'Please select the city', trigger: 'change' }
        ]
      }
    }
  },
  mounted () {
    
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('Success!');
        } else {
          this.$Message.error('Fail!');
        }
      })
    },
  }
}
</script>
<style lang="less">
.upload_writ {
  
}
</style>